/**
     jobParams -> 
     {0 : jobId},  {1 : prevHashReversed},  {2 : generationTx1},  {3 : generationTx2},  {4 : merkleBranch},  {5 : rpcVersion},  {6 : rpcBits},  {7 : curTime}
*/
import { hexStringToUint8Array, sha256dString, uint8ArrayToHex } from "./util";
export async function generateHeaderTemplate(jobParams, extraNonce1, extraNonce2, nTime) {
    var nTimeBuffer = hexStringToUint8Array(nTime);
    var nBitsBuffer = hexStringToUint8Array(jobParams[6]);
    var coinbaseHash = await coinbaseHashDigest(jobParams[2], jobParams[3], extraNonce1, extraNonce2);
    var merkleRootForward = await calcMerkleRoot(jobParams[4], coinbaseHash);
    var merkleRootReversed = await reverseStringLikeBuffer(merkleRootForward);
    var merkleRootBuffer = hexStringToUint8Array(merkleRootReversed);
    var prevBlockHashBuffer = hexStringToUint8Array(jobParams[1]);
    var versionBuffer = hexStringToUint8Array(jobParams[5]);
    var serializedHeader = await serializeHeader(nTimeBuffer, nBitsBuffer, merkleRootBuffer, prevBlockHashBuffer, versionBuffer);
    var serializedHeaderReversed = await reverseStringLikeBuffer(serializedHeader);
    // console.log(`Serialized Header : ${serializedHeaderReversed}`);
    return serializedHeaderReversed;
}

async function serializeHeader(nTime, nBits, merkleRoot, prevHash, version) {
    var header = new Uint8Array(76);
    header.set(nBits, 0);
    header.set(nTime, 4);
    header.set(merkleRoot, 8);
    header.set(prevHash, 40);
    header.set(version, 72);
    return uint8ArrayToHex(header);
}
export async function calcMerkleRoot(merkleBranch, coinbaseHash) {
    var firstStr = [coinbaseHash, merkleBranch[0]].join('');
    var firstHash = sha256dString(firstStr);
    var merkleCopy = [firstHash];
    for(let i = 1 ; i<merkleBranch.length; i++){
        merkleCopy.push(merkleBranch[i]);
    }
    var temp;
    for(let i=0; i < merkleCopy.length; i++){
        if( i === 0 ){
            temp = merkleCopy[0];
        } else {
            temp = sha256dString([temp, merkleCopy[i]].join(''));
        }
    }
    return temp;
}
/**
 * @param {*} genTx1 from generationTransaction[0]
 * @param {*} genTx2 from generationTransaction[0]
 * @param {*} extraNonce1
 * @param {*} extraNonce2
 * @description convert extraNonce1,2 to Buffer 
 * @returns 
 */
async function coinbaseHashDigest(genTx1, genTx2, extraNonce1, extraNonce2) {
    var coinbaseArray = [genTx1, extraNonce1, extraNonce2, genTx2];
    var coinbaseString = coinbaseArray.join('');
    return sha256dString(coinbaseString);
}
/**
 * @description if string = 'a2b4', reversedStringLikeBuffer = 'b4a2';
 * @param {*} forwardString 
 */
export async function reverseStringLikeBuffer(forwardString) {
    if(forwardString.length % 2 !== 0){
        console.log("Wrong Forward String Length");
        return;
    }
    var reversed = [];
    for (let i = forwardString.length-1; i >= 0; i-=2){
        reversed.push(forwardString[i-1], forwardString[i]);
    }
    return reversed.join('');
}