import React, { useEffect, useRef } from 'react';
import { io } from 'socket.io-client';
import { calcHeaderHash } from './headerHash';
import { getnTime } from './util';
import { Button } from './button';
import { mining } from './shader';
import { generateHeaderTemplate, reverseStringLikeBuffer } from './headerTemplate';

function receiveNonceAndDiff(res) {
  return [res.result[1], res.result[0][0][1]];  // [ extraNonce, difficulty ]
}

function sendMessage(socket, id, method, params) {
  const data = JSON.stringify({
    id: id,
    method: method,
    params: params
  });
  socket.emit('message', data);
}

function App() {
  const socketRef = useRef(null);
  /**
     jobParams -> 
     {0 : jobId},  {1 : prevHashReversed},  {2 : generationTx1},  {3 : generationTx2},  {4 : merkleBranch},  {5 : rpcVersion},  {6 : rpcBits},  {7 : curTime}, {8 : targetHash}
   */
  var jobParams = [];
  var targetHash = '0000000f0000f0000000f0000000f0000000f0000000f0000000f0000000f000';
  var header;
  var difficulty;
  var extraNonce1;
  var headerHash;
  var answerNonce;
  var deviceOption = 'none';
  var miningState = [false, false];
  var peerId = false;
  var ExtraNonce2 = function ExtraNonce2() {
    this.position = 0;
    this.increment = function () { this.position++; }
    this.current = function () { return this.position.toString(16).padStart(8, '0'); }
  }

  useEffect(() => {

    peerId = localStorage.getItem('peerId');
    if (!peerId) {
      peerId = [...crypto.getRandomValues(new Uint8Array(8))]
        .map(b => b.toString(16).padStart(2, '0'))
        .join('');
      localStorage.setItem('peerId', peerId);
      console.log(peerId);
    }

    socketRef.current = io("http://117.16.244.34:8084", {
      query: { peerId }
    });

    const socket = socketRef.current;

    socket.on('message', async (res) => {
      switch (res.method) {
        case 'mining.notify':
          jobParams = res.params;
          // targetHash = jobParams[8]; // Real Target vs Test Target
          console.log(`JobParams Received { jobId : ${jobParams[0]} }`);
          miningState[0] = true;
          break;
        case 'mining.extraNonceAndDiff':
          [extraNonce1, difficulty] = receiveNonceAndDiff(res);
          break;
        case 'mining.authorizationConfirm':
          console.log("authorization confirmed");
          miningState[1] = true;
          break;
        case 'submit.authError':
          console.log(res.method);
          break;
        case 'submit.noExtraNonce':
          console.log(res.method);
          break;
        case 'submit.responseResult':
          if (res.result) {
            console.log(`Submit Response : ${res.result}`);
          } else {
            console.log(`Submit Response : ${res.error[1]}`);
          }
          break;
        case 'mining.set_difficulty': break;
        default: break;
      }

      socket.on('difficultyUpdate', (msg) => {
        console.log(msg);
      })
    });

    return () => {
      socket.disconnect();
    };
  }, []);

  function sendSubscribe() {
    const id = "SocketId";
    const method = "mining.subscribe";
    const params = [];
    sendMessage(socketRef.current, id, method, params);
  }

  function sendAuth() {
    const id = "SocketId";
    const method = "mining.authorize";
    const params = ['bc1q4h5wttwul3mh8h8u7xp0vxqrezm68mc5utx039', 'rtppwd'];
    sendMessage(socketRef.current, id, method, params);
  }

  function sendSubmit(nTime, nonce, extraNonce2) {
    const id = "SocketId";
    const method = "mining.submit";
    /**
     *  {1 : name}, {2 : jobId}, {3 : extraNonce2}, {4 : nTime}, {5 : nonce}
     */
    const params = ['rockthepeople', jobParams[0], extraNonce2, nTime, nonce];
    sendMessage(socketRef.current, id, method, params);
  }

  function setDeviceOption(value) {
    deviceOption = value;
  }

  async function runWebGPU() {
    if (!miningState[0] || !miningState[1] || deviceOption === 'none') {
      alert(`Authorization, Subscription, Set deviceOption First!`);
      return;
    }
    var extraNonce2 = new ExtraNonce2();
    console.log(`⛏️🪙 Start Mining (Big Epoch) `);
    for (let j = 0; j < 100; j++) {
      var nTime = getnTime();
      console.log(`⛏️🪙 Little Epoch Start, extraNonce2 : ${extraNonce2.current()}`)
      header = await generateHeaderTemplate(jobParams, extraNonce1, extraNonce2.current(), nTime);
      console.log(header);
      for (let i = 0; i < 100; i++) {
        var miningResult = await mining(header, targetHash, i, deviceOption);
        if (Number(miningResult) > 0) {
          answerNonce = await reverseStringLikeBuffer(miningResult);
          console.log(`✅ Done! Result : \n\tNonce : ${miningResult}\n\tHeader : ${header}`);
          sendSubmit(nTime, answerNonce, extraNonce2.current());
          return;
        }
        if ((i + 1) % 10 === 0) {
          console.log(`\t[${j + 1}] Progress : ${i + 1}%`)
        }
      }

      extraNonce2.increment();

    }
    console.log(`No Result... Try with another extraNonce2`);
  }

  async function tryMining() {
    var nTime = getnTime();
    var extraNonce2 = new ExtraNonce2();
    // todo - 250126 이걸 webGPU에서 할 수 있도록... 바꿔야함!
    var nonce = Math.pow(2, 31).toString(16);
    if (difficulty !== undefined && extraNonce1 !== undefined && extraNonce1.length === 8) {
      headerHash = await calcHeaderHash(jobParams, extraNonce1, extraNonce2.current(), nTime, nonce);
      if (headerHash) {
        console.log(`Calculated Header \n\tHash : \t\t\t${headerHash}\n\tTarget : \t\t${jobParams[8]}\n\tNonce : \t\t${nonce}\n\tExtraNonce1 : \t${extraNonce1}\n\tExtraNonce2 : \t${extraNonce2.current}`);
        sendSubmit(nTime, nonce);
      }
    }
  }

  return (
    <div style={{ color: 'white' }}>
      <div style={{ display: 'flex', flexDirection: 'column', padding: '20px 30px', gap: '10px' }}>
        <h1>Bitcoin web-based mining system</h1>
        <button style={{ display: 'none', width: 'fit-content', fontSize: '24px', color: 'white', backgroundColor: '#252525' }} onClick={() => socketRef.current.connect()}>
          Connect Socket
        </button>
        <select defaultValue={'none'} style={{ fontSize: '24px', width: 'fit-content' }} onChange={(e) => setDeviceOption(e.target.value)}>
          <option value={'none'}>None</option>
          <option value={'m3pro'}>m3pro</option>
          <option value={'m1air'}>m1air</option>
        </select>
        <p>---- Order : Auth, Subscription, Mining ----</p>
        <Button clickEvent={sendAuth} name={`Pool Authorization`}></Button>
        <Button clickEvent={sendSubscribe} name={'Pool Work Subscribe'}></Button>
        <Button clickEvent={runWebGPU} name={'Mining webGPU'}></Button>
        <p>---- Buttons under here for manual trial ----</p>
        <Button clickEvent={tryMining} name={'Mining Trial (1)'}></Button>
        <Button clickEvent={sendSubmit} name={'Manual Submit'}></Button>
      </div>
    </div>
  );
}

export default App;